import React, {useState, useEffect} from 'react';
import {Button} from './Button';
import {Link} from 'react-router-dom';
import './navbar.css';



function Navbar() {
  const [Click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!Click);
  const closemobilemenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };
  
  useEffect(() => {
    showButton();
  }, []);
  
  window.addEventListener('resize', showButton);
   
  return (
  <>
    <nav className="Navbar">
      <div className="Navbar-container">
          <Link to="/" className="Navbar-logo" onClick={closemobilemenu} >
         <img className="fa-monero" src="https://raw.githubusercontent.com/mo214/reactwebsite/main/name%20tag%202.jpg"/>
          </Link>
          <div className='menu-icon' onClick={handleClick}>
            <i className={Click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={ Click ? 'nav-menu active' : 'nav-menu'}>
             <li className='nav-item'>
               <Link to='/' className='nav-links' onClick={closemobilemenu}>
                 Home
               </Link>
             </li>
             <li className='nav-item'>
               <Link to='Aboutme' className='nav-links' onClick={closemobilemenu}>
                 About me
               </Link>
             </li>
             <li className='nav-item'>
               <Link to='Education' className='nav-links' onClick={closemobilemenu}>
                 Education
               </Link>
             </li>
             <li className='nav-item'>
               <Link to='Work-history' className='nav-links' onClick={closemobilemenu}>
                  Experience 
               </Link>
             </li>
             <li className='nav-item'>
               <Link to='Portforlio' className='nav-links' onClick={closemobilemenu}>
                  Portfolio
               </Link>
             </li>
             <Link to='sign-up' className='nav-links-mobile' onClick={closemobilemenu}>
               Contact
             </Link>
          </ul>
          {button && <Button buttonStyle='btn--outline'>Contact</Button>}

      </div>
    </nav>
</>

    );
}

export default Navbar
